<!--
  @auth:lcw
  @date:2020-9-15
  数据反查弹框
-->
<template>
  <transition name="fade">
    <div class="pop" v-if="popListsShow" @click.self="popListsShow = false">
      <div class="pop-wrapper" :style="{marginTop:popMarginTop+'px'}">
        <div class="inner">
          <ul class="list-ul list-title clearfloat">
            <li v-for="(item,index) of Object.values(data.map)" :key="index" :style="{width:columnW[index]+'px'}">
              {{item}}
            </li>
          </ul>
          <el-scrollbar :style="{height:list.length>singlePageNum?liItemH*singlePageNum+'px':list.length*liItemH+'px'}">
            <div class="lists-container" :style="{height:data.length*liItemH+'px'}"  v-if="list.length">
              <ul class="list-ul clearfloat" v-for="item of list" :key="item.a">
                <li v-for="(it,i) of Object.keys(data.map)" :key="it" :style="{width:columnW[i]+'px'}">
                  {{item[it]}}
                </li>
              </ul>
            </div>
          </el-scrollbar>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  export default {
    name: "index",
    props: {
      data: {
        type: Object,
        default: {}
      }
    },
    data() {
      return {
        popListsShow: false,
        singlePageNum: 10,//单页行数
        popMarginTop: 0,  //顶部top值
        liItemH: 70,      //行高
        columnW: [220, 130, 130, 130, 230, 150, 140, 130, 130, 210] //列宽
      }
    },
    mounted() {
      let dataLength = this.list.length > this.singlePageNum ? this.singlePageNum : this.list.length;
      this.popMarginTop = (1080 - (dataLength + 1) * this.liItemH - 56) / 2;
    },
    computed: {
      list() {
        return this.data.data
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .fade-enter-active, .fade-leave-active
    transition opacity .3s

  .fade-enter, .fade-leave-to
    opacity 0

  .pop /deep/ .el-scrollbar__thumb
    background-color #29adff

  .pop
    position fixed
    top 0
    left 0
    z-index 100000
    width 1920px
    height 1080px
    background rgba(0, 0, 0, 0.6)

    .pop-wrapper
      position relative
      width 1602px
      margin 0 auto
      border-radius 10px
      overflow hidden
      border 1px solid #27a4f3
      box-shadow 0 0 20px rgba(0, 174, 255, 0.4) inset
      background url("./img/pop-bg.png") no-repeat
      background-size 100% 100%
      .inner
        ul.list-ul
          &:nth-child(odd)
            background-color rgba(161, 151, 255, 0.1)

          &:nth-child(even)
            background-color transparent

          li
            box-sizing border-box
            border 1px solid #585a9e
            border-right none
            border-top none
            float left
            line-height 70px
            text-align center
            font-size 16px
            color #cbfbff
            overflow hidden
            text-overflow ellipsis
            white-space nowrap
            user-select none
            &:last-child
              border-right 1px solid #130a3e

        ul.list-title
          background-color transparent !important
          li
            font-family '黑体'
            font-size 18px
            color #36edf7
</style>
